import styles from './PetHotel.module.css'
import React from 'react'
import video from '../assets/video/pethotel.mp4'
import { SRLWrapper } from "simple-react-lightbox";
import {PATH, images}  from '../data/petHotelImages'
import Button from '../components/UI/Button'

const PetHotel = props => {

    return (
        <React.Fragment>
            <div className={styles.Header}>
                <h2>Hotel para ces</h2>
            </div>
            <div className={`container ${styles.Container}`} >
                <p>No pet_hotel_daquintamiguel, o seu co pode hospedar-se num dos 666 amplos alojamentos individuais, todos com zona coberta, onde se encontram confortveis camas, e equipados com bebedouros automticos. Todos os nossos hspedes usufruem diariamente de parques de passeio individuais, com a possibilidade de banho numa agradvel piscina para brincadeiras, sempre ao som de msica ambiente que serviro de cenrio perfeito para umas frias inesquecveis.</p><br />
                <p>Os hspedes da Quinta tm um constante acompanhamento mdico-veterinrio, incluindo tambm tratamentos ou medicaes a hspedes em recuperao, irresistveis refeies e seguro de responsabilidade civil.</p><br />
                <p>Zelando pela sade dos nossos simpticos hspedes, solicitamos a todos que tragam a Caderneta de Vacinas. As vacinas do seu amigo (incluindo a vacina especfica contra a chamada tosse de canil ) devero estar em dia e ter sido administradas com, pelo menos, 5 dias de antecedncia em relao  sua data de entrada.</p><br />
                <p>Para mais informaes ou reservas contacte-nos para <a href="#">geral@dinclitaspirit.pt</a> ou atravs do nmero 91 379 50 80</p>
                <br />
                <br />
                <h3>COMODIDADES (EXTRA)</h3>
                <br />
                <ul>
                    <li>Box com aquecimento</li>
                    <li>Sesso de lazer divertida no Relvado/Piscina exterior</li>
                    <li>Play Time Extra</li>
                    <li>Banho no dia de sada</li>
                    <li>Dietas especiais incluindo at arroz com carne</li>
                    <li>Tratamentos de enfermagem veterinria</li>
                    <li>Treinos de Obedincia</li>
                </ul>
                <Button text="Saber mais" margin="left" />
                <div className={styles.video}>
                    <video controls>
                        <source src={video} type="video/mp4"></source>
                    </video>
                </div>
                <div className={styles.galleryWrapper}>
                    <h2>Galeria de imagens</h2>
                    <div className={styles.gallery}>
                        <SRLWrapper>
                            {images.map((img, idx) => <img className={styles.galleryItem} src={`${PATH}${img}`} key={idx} alt={img} />)}
                        </SRLWrapper>
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
}

 
export default PetHotel